import axios from 'axios';

const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:3000/api';

export const getFarmers = async () => {
    const response = await axios.get(`${API_BASE_URL}/getFarmers`);
    return response.data;
};

export const registerFarmer = async (accountName, nickname) => {
    const response = await axios.post(`${API_BASE_URL}/registerFarmer`, { accountName, nickname });
    return response.data;
};

export const unregisterFarmer = async (accountName) => {
    const response = await axios.post(`${API_BASE_URL}/unregisterFarmer`, { accountName });
    return response.data;
};

export const getInventory = async (actor) => {
    const response = await axios.get(`${API_BASE_URL}/inventory/${actor}`);
    return response.data;
};

export const verifyLogin = async (loginData) => {
    const response = await axios.post(`${API_BASE_URL}/verifyLogin`, loginData);
    return response.data;
};

export default {
    getFarmers,
    registerFarmer,
    unregisterFarmer,
    getInventory,
    verifyLogin,
};
